import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-account-statement01',
  templateUrl: 'account-statement01.html',
})
export class viewAccStatement01 {
  accounts = [];
  accStmntError01;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil : CryptoParamUtilProvider,
              public storage : LocalStorageProvider,
              public gaProvider : GoogleAnalyticsProvider) {

    let dAccount = this.storage.retrieveFromStorage('accounts');
    dAccount = cryptoUtil.getDecryptVal(dAccount);
    this.accounts = JSON.parse(dAccount);
  }

  viewAccountStatementAction = function (account) {
    this.accStmntError01 = "";
    this.navCtrl.push('AccountStatement02Page', {'accountNo': account.accountNo});
  };


  ionViewDidLoad() {
    // console.log('ionViewDidLoad AccountStatement01Page');
    this.gaProvider.startTrackerWithId("Account Statement");
  }

}



